<!--
 * @Autor: linxu
 * @Date: 2022-04-11 14:06:05
 * @LastEditors: linxu
 * @LastEditTime: 2022-05-19 09:18:58
 * @Description: 用户反馈列表
-->
<template>
  <div>
    <BMainTopComponent />
    <div class="user-feedback">
      <!-- 筛选 -->
      <div class="search-box">
        <!-- <div class="search-item">
          <span>工单号:</span>
          <el-input placeholder="请输入工单号" v-model="searchForm.num" clearable> </el-input>
        </div> -->
        <div class="search-item">
          <span>反馈人:</span>
          <el-input placeholder="请输入姓名" v-model="searchForm.name" clearable> </el-input>
        </div>
        <div class="search-item">
          <span>工单分类:</span>
          <el-select
            v-model="searchForm.type"
            :popper-append-to-body="false"
            placeholder="请选择工单分类"
          >
            <el-option label="全部" value="-1"></el-option>
            <el-option
              v-for="(item, index) in WorkOrderTypeEnum"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span>工单状态:</span>
          <el-select
            v-model="searchForm.Status"
            :popper-append-to-body="false"
            placeholder="请选择工单状态"
          >
            <el-option label="全部" value="-1"></el-option>
            <el-option
              v-for="(item, index) in WorkOrderStatusEnum"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="search-btn-box">
          <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
          <el-button type="primary" class="query-btn" @click="queryTableData()">查 询</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <TableList
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
        border
        v-loading="loading"
      >
        <el-table-column
          slot="operation"
          label="操作"
          align="center"
          v-bind="options && options.props"
        >
          <template slot-scope="scope">
            <div class="operate-group">
              <el-button type="text" @click="handleShow(scope.row)"> 查看 </el-button>
              <el-button v-if="scope.row.Status !== 3" type="text" @click="handleClose(scope.row)">
                关闭
              </el-button>
              <!-- <el-button type="text" @click="handleDelete(scope.row)"> 删除 </el-button> -->
            </div>
          </template>
        </el-table-column>
      </TableList>
    </div>
  </div>
</template>

<script>
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import _data from './data.js'
const { WorkOrderTypeEnum, WorkOrderStatusEnum } = _data

export default {
  name: 'UserFeedbackList',
  mixins: [paginationConfigMixin],
  data() {
    return {
      loading: false,
      // 筛选条件
      searchForm: {
        name: '',
        type: '-1',
        Status: '-1'
      },
      WorkOrderTypeEnum,
      WorkOrderStatusEnum,
      // 表格
      columns: _data.userFeedbackColumns(this),
      dataSource: [],
      options: {
        height: '100%',
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 126 // 操作列宽度
        }
      },

      crewManageVisible: false // 添加机组弹窗
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      this.loading = true
      const query = {
        Page: this.paginationCfg.currentPage,
        PageSize: this.paginationCfg.pageSize
      }
      const params = { ...this.searchForm }
      console.log(params, query)
      // 客户反馈
      this.$api.operation
        .getGensetProjectFeedbackManage(params, query)
        .then((res) => {
          if (res.Code === 1) {
            this.dataTotal = res.Data.TotalItemCount
            this.$set(this, 'dataSource', res.Data.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 重置筛选条件
    reset() {
      this.$set(this, 'searchForm', {
        name: '',
        type: '-1',
        Status: '-1'
      })
    },

    // 查看
    handleShow(row) {
      this.$router.push({
        path: '/operation/feedbackDetails',
        query: {
          feedbackId: row.FeedbackId,
          feedBackInfo: row
        }
      })
    },

    // 关闭
    handleClose(row) {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '关闭工单',
        message: h('div', [h('p', [h('span', '确定关闭当前工单吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = {
            id: row.FeedbackId
          }
          this.$api.operation
            .closeGensetProjectFeedback({}, query)
            .then((res) => {
              if (res.Code === 1) {
                this.$message({
                  type: 'success',
                  message: '关闭工单成功!'
                })
                this.queryTableData()
              } else {
                this.$message({
                  type: 'error',
                  message: '关闭工单失败，请重新尝试!'
                })
              }
            })
            .catch((err) => {})
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }

    // 删除
    // handleDelete(row) {
    //   console.log(row)
    //   this.recordId = row.key
    // }
  }
}
</script>

<style lang="less" scoped>
.user-feedback {
  .search-box {
    .search-item {
      // width: 20%;
    }
  }
}
</style>



